@import url('https://fonts.googleapis.com/css?family=Audiowide');

* {
    margin: 0;
    padding: 0;
}

body {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #0d1019;
    font-family: "Audiowide";
}

.model-builder-page {
    width: 100%;
    height: 89.5vh;
}

.model-builder-container {
    width: 100%;
    height: 100%;
}

.model-builder-div {
    width: 100%;
    height: auto;
    border-right: 2px solid #262830;
    background-color: #1a1c24; 
}

.model-builder-header-div {
    width: 100%;
    background-color: #131314; 
    border-right: 2px solid #262830;
    border-bottom: 2px solid #0984e3;
}

.model-builder-header {
    height: auto;
    width: 50%;
    display: inline-block;
}


.make-predictions-body, .active-model {
    width: 100%;
    height: auto;
    border: 1px solid #262830;

}

.make-predictions-body {
    display: inline-flex;
}

.active-model {
    height: 30px;
    font-size: 24px;
    display: inline-flex;
    justify-content: center;
}

.make-predictions-form {
    width: 50%;
    border-right: 1px solid #262830;
    background-color: #131314; 
}

.make-predictions-output {
    width: 50%;
    background-color: black;
}

.make-predictions-output p {
    color: white;
    margin: 5px;
}

.active-model h4 {
    margin: auto;
    color: white;
    border-bottom: 1px solid #0984e3;;
    font-size: 18px;
    font-weight: 100;
}

.model-builder-header-row {
    width: 100%;
    display: inline-flex;
    justify-content: left;
}

.model-builder-header-row p {
    color: #929499;
    width: 100%;
    font-weight: 100;
    font-size: 12px;
    margin-right: 10px;
    margin-left: 10px;
    margin-bottom: 10px;
}

.model-builder-header h2 {
    margin-top: auto;
    margin-bottom: auto;
    margin-right: auto;
    margin-left: 10px;
    color: white;
    font-size: 24px;
    font-weight: 100;
}

.model-builder-body {
    display: inline-flex;
    margin: auto;
    width: calc(100% - 20px);
    margin: 10px;
    height: calc(89vh - 75px);
    background-color: #1a1c24;
    border-radius: 5px;
}

.make-predictions-div{
    margin-left: 10px;
    width: 33%;
    height: 100%;

    border: 1px solid #262830;
}


.make-predictions-div input, .make-predictions-div option, .make-predictions-div select{
    font-family: "Audiowide";
    font-weight: 100;
}

.select-div {
    margin-left: 5px;
    display: inline-flex;
    width: auto;
    
}

.select-div .endzone-select{
    margin: auto;
}

.select-div h2 {
    font-size: 16px;
    width: 100%;
    margin-top: auto;
    margin-bottom: auto;
    color: white;
    font-weight: 100;
}

.make-predictions-body .form-element {
    width: 100%;
    margin-top: 10px;
    margin-left: 10px;
    margin-bottom: auto;
    display: inline-flex;
}

.make-predictions-body .form-element h4 {
    margin-right: 10px;
    color: white;
    font-weight: 100;
}

@media (max-width: 800px) {
    .model-builder-header {
        width: 100%;
    }

    .model-builder-header-row {
        justify-content: left;
    }  

    .model-builder-div {
        width: 100%;
    }

    .model-builder-body {
        display: inline-block;
    }

    .make-predictions-div {
        margin-top: 10px;
        margin-left: 0px;
        width: 100%;
    }
}
