.create-model-div {
    width: 250px;
    height: auto;
}

.create-model-div .form-element {
    width: 100%;
}

.create-model-div .form-element h4 {
    font-size: 16px;
    color: white;
    font-weight: 100;
    margin-left: 5px;
}

.create-model-div .form-element h4:hover {
    cursor: help;
}

.create-model-div .form-element .check-input {
    margin-left: 5px;
    width: 24px;
    height: 24px;
}

.divider-div {
    border: 1px solid #0984e3;
    margin-top: 5px;
    margin-bottom: 5px;
}

.create-model-div .form-element .text-input {
    margin-left: 6px;
    font-family: 'Audiowide';
    font-size: 16px;
    width: calc(100% - 16px);
}