@import url('https://fonts.googleapis.com/css?family=Audiowide');

* {
    margin: 0;
    padding: 0;
}

body {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #0d1019;
    font-family: "Audiowide";
}

.alert-div {
    position: fixed;
    top : 0vh;
    left : 0;
    margin : auto;
    width : 100vw;
    z-index : 10000;
}